<template>
  <div class="test">
    <div class="custom-card">
      <div class="card" style="width: 18rem; margin-bottom: 25px; margin-right: 25px">
        <img
            style="height: 286px; width: 286px"
            class="card-img-top"
            :src="'http://localhost:8080/files/' + product.image"
            alt=""
            @click="$router.push({ name: 'product-page', params: { id: product.id } })"
        >
        <div class="card-body">
          <h5 class="card-title">{{ product.name }}</h5>
          <p class="card-text">  . {{product.price}} .</p>
          <p style="color: crimson" v-if="product.amount === 1"> !   </p>
          <p v-else-if="product.amount === 0">  </p>
          <p v-else> -  : {{product.amount}} .</p>
          <div v-if="product.sale !== null">
            <p> {{product.sale.discount}}%</p>
            <p>    {{priceWithDiscount}}</p>
          </div>
        </div>
        <div class="card-footer">
          <button v-if="product.amount === 0" class="btn btn-secondary">  </button>
          <button v-else class="btn btn-primary" @click="addProductToCart">  </button>
        </div>
      </div>
    </div>
  </div>



</template>

<script>
import CartService from '../services/cart.service'

export default {
  props: {
    product: {
      type: Object
    }
  },
  data() {
    return {
      priceWithDiscount: 0
    }
  },
  methods: {
    calculateDiscount() {
      if (this.product.sale !== null) {
        this.priceWithDiscount = (this.product.price / 100) * (100 - this.product.sale.discount);
      }

    },
    addProductToCart() {
      let cartDTO = {
        "productId": this.product.id,
        "amount": 1
      }
      CartService.addProductToCart(cartDTO);
    }
  },
  created() {
    this.calculateDiscount();
  }
}
</script>

