<template>
  <div class="orders-component">
    <div class="order-card" v-on:click="changeVisible">
      <h2>  {{order.orderId}}   {{order.summary}} .</h2>
      <div v-if="isVisible">
        <p>:</p>
          <div v-for="productsInfo in order.productsInfo"
               :key="productsInfo"
          >
            <p>{{productsInfo}}</p>
          </div>
      </div>
      <p v-if="isVisible"> : {{order.orderType}}</p>
      <p v-if="isVisible"> : {{order.orderStatus}}</p>
      <p v-if="isVisible"> : {{order.date}}</p>
      <button type="button" class="btn btn-danger" @click="modifyOrderEvent"> </button>
    </div>
  </div>
</template>

<script>
import { eventBus } from '@/main'

export default {
  props: {
    order: {
      type: Object
    }
  },
  data() {
    return {
      isVisible: false,
    }
  },
  methods: {
    modifyOrderEvent() {
      eventBus.$emit('modifyOrderEvent', this.order.orderId)
    },
    changeVisible() {
      this.isVisible = !this.isVisible
    }
  }
}
</script>